/* eslint-disable react/no-danger-with-children */
import styled from 'styled-components';
import Tooltip from '../Tooltip/Tooltip';
import { MD5, isMobile } from '../../utils/utils';


const Wrapper = styled.div`
  display:${props => props.block};
  .text {
    border-bottom : 1px dashed #93A1B8;
    width : max-content;
    cursor : pointer;
    display:${props => props.block}
  }
`


export default function UnderlineText({ text, id = MD5(text), tip, multiline, html, className, block = 'block', width = 'auto', children, tiggerEvent }) {
  const calculatePosition = (position, currentEvent, currentTarget, refNode, place, desiredPlace, effect, offset) => {
    const rect = currentTarget.getBoundingClientRect();
    const tooltipNodeRect = refNode.getBoundingClientRect();
    //位置被遮盖
    let { x: left, y: top, height } = rect
    const { left: originLeft, top: originTop } = position
    // top = top + height
    if (originLeft + tooltipNodeRect.width > document.documentElement.clientWidth) {
      left = originLeft - tooltipNodeRect.width + rect.width;
    } else {
      left = originLeft
    }
    if (originTop + tooltipNodeRect.height >= document.documentElement.clientHeight) {
      top = originTop - tooltipNodeRect.height + height / 2
    } else {
      top = originTop
    }
    if (left < 0) {
      left = 0
    }
    if (top < 0) {
      top = 0
    }
    return { top: top, left: left }
  }
  return (
    <Wrapper className={className} block={block}>
      <div className='text' data-for={id} data-tip={tip} data-event={isMobile() ? 'click' : tiggerEvent} data-html={html} >
        {text || children}
      </div>
      {tip && <Tooltip id={id} width={width} padding='12' place='bottom' overridePosition={calculatePosition} html={html} borderRadius={8} multiline={multiline}>
      </Tooltip>}
    </Wrapper>
  )
}